/* General Styling */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f7f6;
  color: #333;
  line-height: 1.6;
}

h1 {
  text-align: center;
  color: #333;
  margin-top: 40px;
}

.main-content {
  display: flex;
  justify-content: space-around;
  margin: 40px auto;
  padding: 20px;
  width: 80%;
  max-width: 1200px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
}

form {
  width: 100%;
  display: flex;
  flex-direction: column;
}

form label {
  font-weight: bold;
  margin-bottom: 8px;
}

form input, form select, form button {
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 16px;
}

form input[type="text"], form input[type="date"], form select {
  width: 100%;
}

form button {
  background-color: #2196F3;
  color: white;
  cursor: pointer;
  border: none;
  font-size: 18px;
}

form button:hover {
  background-color: #1976D2;
}

form button:disabled {
  background-color: #b0bec5;
  cursor: not-allowed;
}

.error {
  color: red;
  margin-bottom: 10px;
  font-size: 14px;
}

/* Table Styling */
table {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
}

table th,
table td {
  padding: 12px 15px;
  text-align: left;
  border: 1px solid #ddd;
}

table th {
  background-color: #2196F3;
  color: white;
}

table tr:hover {
  background-color: #f1f1f1;
}

.present {
  background-color: #4CAF50;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
}

.absent {
  background-color: #f44336;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
}

button.edit-btn {
  background-color: #FF9800;
  color: white;
  padding: 5px 10px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

button.edit-btn:hover {
  background-color: #F57C00;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .main-content {
    flex-direction: column;
    align-items: center;
  }

  table {
    width: 100%;
    margin-top: 20px;
  }

  form button {
    font-size: 16px;
  }

  form input,
  form select {
    font-size: 14px;
  }

  h1 {
    font-size: 28px;
  }

  .attendance-list {
    margin-top: 20px;
  }
}
